<template>
  <v-panel
    :first="{
      label: 'All transactions',
      size: 'full',
    }"
  >
    <template #form-wrap>
      <Transactions :loading="loading" :value="transactions || []" />
    </template>
  </v-panel>
</template>

<script>
import Transactions from '../components/Transactions.vue'
import { mapGetters } from 'vuex'

export default {
  name: 'History',
  data: () => ({
    transactions: false,
    loading: true,
  }),
  computed: {
    ...mapGetters({
      profile: 'auth/profile',
    }),
  },
  components: {
    Transactions,
  },
  mounted() {
    this.loading = false
    this.transactions = this.profile.transactions
  },
}
</script>

<style lang="sass" scoped>
@import "../../../../scss/smart-grid"
@import "../../../../scss/variables"

.form-group
  display: flex
  flex-wrap: wrap
  justify-content: space-between
  margin-bottom: 20px

  @include xs-block
    flex-direction: column
    width: 100%
  @include sm-block
    flex-direction: column
    width: 100%
  @include md-block
    flex-direction: row
    width: 80%
  @include lg-block
    flex-direction: row
    width: 80%

  *:first-child
    margin-right: 10px

.pay_button
  margin-top: 20px
  width: 100px

  @include xs-block
    width: calc(100% - 38px)
  @include sm-block
    width: calc(100% - 38px)
  @include md-block
    width: 100px
  @include lg-block
    width: 100px
</style>
