<template>
  <v-panel :css="{ background: '#00042277', border: '1px #00115577 solid' }">
    <template #form-wrap>
      <div class="support__chat">
        <div class="support__chat_body">
          <div
            v-for="(message, i) in messages"
            :key="i"
            :style="{
              paddingTop: !message.delivered ? '1.2rem' : '0.657rem',
            }"
            :class="[
              'support__chat_body_message ',
              message.yours ? 'support__chat_body_message-yours' : '',
            ]"
          >
            <font-awesome-icon
              class="support__chat_body_message-info"
              v-if="!message.delivered"
              :style="{
                left: message.yours ? 'auto' : '0.5rem',
                right: message.yours ? '0.5rem' : 'auto',
              }"
              icon="spinner"
              spin
            ></font-awesome-icon>
            {{ message.text }}
            <span
              class="support__chat_body_message-date"
              v-if="message.delivered"
              :style="{
                opacity: 0.4,
                textAlign: message.yours ? 'right' : 'left',
              }"
            >
              {{ message.date }}
            </span>
          </div>
        </div>
        <div class="support__chat_message-text">
          <input
            v-model="messageInput"
            placeholder="Enter you message..."
            type="text"
            @keydown.enter="sendMessage"
          />
          <button @click="sendMessage">send</button>
        </div>
      </div>
    </template>
  </v-panel>
</template>

<script>
export default {
  name: 'Support',
  data: () => ({
    messageInput: '',
    messages: [],
  }),
  methods: {
    scrollToBottom() {
      const body = this.$el.querySelector('.support__chat_body')
      body.scrollTop = body.scrollHeight - body.clientHeight
    },
    sendMessage() {
      let i = this.messages.length

      this.messages.push({
        yours: true,
        text: this.messageInput,
        delivered: false,
      })

      this.axios
        .post(
          window.location.protocol +
            '//' +
            window.location.host +
            '/api/support',
          {
            token: sessionStorage.getItem('auth-token'),
            message: this.messageInput,
          },
        )
        .then(({ data }) => {
          this.messages[i].delivered = true
          this.messages[i].date = data.message.date
        })
        .catch(err => {
          if (err.response) {
            this.handleError(err.response)
          }
        })

      this.messageInput = ''
      setTimeout(() => {
        this.$el.querySelector('.support__chat_message-text input').focus()
        this.scrollToBottom()
      }, 10)
    },
    fetchMessages() {
      this.axios
        .get(
          window.location.protocol +
            '//' +
            window.location.host +
            '/api/support',
          {
            headers: {
              Authorization: sessionStorage.getItem('auth-token'),
            },
          },
        )
        .then(({ data }) => {
          this.messages = data.messages
          this.scrollToBottom()
        })
        .catch(err => {
          this.handleError(err)
        })
    },
    handleError(err) {
      console.log(err)
    },
  },
  mounted() {
    this.fetchMessages()
  },
}
</script>

<style lang="sass" scoped>
@import "../../../../scss/smart-grid"
@import "../../../../scss/variables"

$chat-message-br: 0.65rem

.support__chat
  &_body
    font-family: 'Montserrat'
    width: 100%
    height: 500px
    border-radius: 10px
    display: flex
    flex-direction: column
    align-items: flex-end
    overflow-y: auto
    padding: 20px 10px
    font-size: .9rem

    &_message
      color: white
      position: relative
      padding: 0.657rem 1.375rem
      margin-bottom: $chat-message-br
      max-width: 70%
      overflow-wrap: break-word
      border-radius: 0.5rem
      border-top-left-radius: 0
      align-self: flex-start
      background-color: $cyan
      transition: padding 0.3s
      text-align: justify
      transition: padding 0.3s
      display: flex
      flex-direction: column

      &-yours
        background-color: $cyan-alpha
        align-self: flex-end
        border-top-right-radius: 0
        border-top-left-radius: 0.5rem

      &-info
        position: absolute
        top: 0.4rem
        font-size: 0.6rem

      &-date
        font-family: sans-serif
        font-size: 0.7rem
        opacity: 0
        transition: opacity 0.5s
        margin-top: .5rem

  &_message-text
    width: 100%
    margin-top: 1.3rem
    display: flex

    input, button
      font-family: 'Montserrat'
      color: white
      border-radius: 5px
      outline: none
      border: none

    input
      margin-right: 1rem
      width: 100%
      font-size: .9rem
      letter-spacing: .05rem
      padding: 10px
      background-color: $blue-alpha

      &::placeholder
        color: $cyan-alpha

    button
      text-transform: uppercase
      width: 100px
      background-color: $cyan-alpha
      box-shadow: 0 2px 5px $cyan-alpha
      transition: all .3s

      &:hover
        box-shadow: none
        background-color: $cyan
        cursor: pointer
        transition: none


.form-group
  display: flex
  flex-wrap: wrap
  justify-content: space-between
  margin-bottom: 20px

  @include xs-block
    flex-direction: column
    width: 100%
  @include sm-block
    flex-direction: column
    width: 100%
  @include md-block
    flex-direction: row
    width: 80%
  @include lg-block
    flex-direction: row
    width: 80%

  *:first-child
    margin-right: 10px
</style>
