<template>
  <v-panel
    title="Transfer"
    label="Transactions History"
    :loading="loading"
    :second="{
      size: 'lg',
    }"
  >
    <template #form-wrap>
      <Form
        :title="'Amount in ' + currency"
        subtitle="min 0.01 (1% comission)"
        type="text"
        value="0.01"
        v-model="amount"
      />
      <Form
        title="Select currency"
        type="select"
        selectWidth="100%"
        :options="Object.keys(wallets).map(currency => currency.capitalize())"
      />
      <Form
        title="Address to transfer"
        type="text"
        value=""
        isAddress="true"
        v-model="address"
      />
      <Button
        class="pay_button"
        theme="secondary_big"
        text="Transfer"
        @click.native="transfer"
      />
    </template>
    <template #second-form-wrap>
      <Transactions :loading="loading" :value="transactions || []" />
    </template>
  </v-panel>
</template>

<script>
import Button from '../Button.vue'
import Form from '../Form.vue'
import Transactions from '../components/Transactions.vue'
import { mapGetters } from 'vuex'

export default {
  name: 'Transfer',
  components: {
    Form,
    Button,
    Transactions,
  },
  data: () => ({
    address: '',
    amount: '0.1',
    loading: false,
  }),
  computed: {
    ...mapGetters({
      profile: 'auth/profile',
      wallets: 'auth/wallets',
      transactions: 'auth/transfers',
      currency: 'popups/exchangeSelectedCurrency',
    }),
  },
  methods: {
    transfer() {
      let amount = +this.amount

      if (isNaN(amount)) {
        this.handleError({
          message: 'Amount must be a number!',
        })
      } else {
        this.loading = true
        this.$axios
          .post('/api/wallet/transfer', {
            recipient: this.address,
            amount,
            currency: this.currency,
          })
          .then(response => {
            this.handleSuccess(response.data)
            this.loading = false
          })
          .catch(err => {
            this.loading = false
            this.handleError(err.response.data)
            console.log(err.response)
          })
      }
    },
    handleError(response) {
      if(response) {
        this.$store.commit('auth/ADD_TRANSACTION', response.transaction)
        this.$store.commit('popups/ADD_ALERT', {
          type: 'error',
          title: 'Transfer failed',
          message: response.message,
        })
      }
    },
    handleSuccess(response) {
      this.$store.commit('auth/ADD_TRANSACTION', response.transaction)
      this.$store.commit('auth/UPDATE_WALLETS', response.wallets)
      this.$store.commit('popups/ADD_ALERT', {
        type: 'message',
        title: 'Transfer succeed!',
        message:
          'You have just successfully sent ' +
          response.transaction.amount +
          ' of ' +
          response.transaction.currency +
          '!',
      })
    },
  },
}
</script>

<style lang="sass" scoped>
@import "../../../../scss/smart-grid"
@import "../../../../scss/variables"

.pay_button
  margin-top: 10px
  width: calc(100% - 38px)
</style>
