<template>
  <div class="transactions-table" v-if="value && value.length && !loading">
    <div
      class="transactions-table__items"
      :style="{
        height: range * 40 + 30 + 'px',
      }"
    >
      <div class="transactions-table__items__title">
        <div class="transactions-table__items__title-amount">
          Date
        </div>
        <div class="transactions-table__items__title-amount">
          Amount
        </div>
        <div class="transactions-table__items__title-currency">
          Currency
        </div>
        <div class="transactions-table__items__title-name">
          Transaction
        </div>
        <div class="transactions-table__items__title-type">
          Type
        </div>
        <div class="transactions-table__items__title-status">
          Status
        </div>
      </div>
      <div
        class="transactions-table__items__item"
        v-for="item in formatedTransactions"
        :key="item._id"
      >
        <div class="transactions-table__items__item-date">
          {{ formatDate(item.unixDate) }}
        </div>
        <div class="transactions-table__items__item-amount">
          {{ item.amount }}
        </div>
        <div class="transactions-table__items__item-currency">
          {{
            { Ethereum: 'ETH', Bitcoin: 'BTC', Litecoin: 'LTC' }[item.currency]
          }}
        </div>
        <div class="transactions-table__items__item-name">
          {{ item.name }}
        </div>
        <div class="transactions-table__items__item-type">
          {{ item.type }}
        </div>
        <div class="transactions-table__items__item-status">
          {{ item.status }}
        </div>
      </div>
    </div>
    <div class="pagination">
      <div v-if="pages.length > 1" class="pages">
        <span class="page_back" @click="goto('back')"
          ><font-awesome-icon icon="arrow-left"
        /></span>
        <span
          v-for="(page, index) in pages"
          :key="index"
          :class="[
            page != '...' ? 'page' : 'page_ellipsis',
            page != '...' && page - 1 == current ? 'page_active' : '',
          ]"
          @click="page != '...' ? goto(page - 1) : false"
          >{{ page }}</span
        >
        <span class="page_back" @click="goto('forward')"
          ><font-awesome-icon icon="arrow-right"
        /></span>
      </div>
    </div>
  </div>
  <NoData
    v-else
    :loading="loading"
    :css="{
      height: range * 40 + 30 + 'px',
    }"
    LT="Your transactions are loading..."
    ET="There is nothing to list"
  />
</template>

<script>
import NoData from './NoData'

export default {
  name: 'Transactions',
  components: {
    NoData,
  },
  data: () => ({
    current: 0,
    maxPagesHalf: 7,
    range: 7,
    statusColors: {
      failed: '#e63946',
      'await approval': 'yellow',
      success: 'green',
    },
  }),
  props: {
    loading: {
      type: Boolean,
      default: false,
    },
    value: {
      type: Array,
      default: [],
    },
  },
  methods: {
    formatDate(date) {
      let global = this.$moment(date).format('YYYY-MM-DD H:mm')
      let locale = this.$moment.utc(global).toDate()

      return this.$moment(locale).format('YYYY-MM-DD HH:mm')
    },
    goto(page) {
      let self = this

      this.$anime({
        targets: '.transactions-table__items__item',
        opacity: [1, 0],
        duration: 200,
        easing: 'easeInSine',
        complete() {
          switch (page) {
            case 'back':
              self.current -= self.current ? 1 : 0
              break
            case 'forward':
              self.current += self.current < self.pagesCount - 1 ? 1 : 0
              break
            default:
              self.current = page
              break
          }

          self.$anime({
            targets: '.transactions-table__items__item',
            opacity: [0, 1],
            duration: 200,
            easing: 'easeOutSine',
          })
        },
      })
    },
  },
  computed: {
    formatedTransactions() {
      let reversed = this.listPageItems
      reversed = reversed.reverse()

      return reversed
    },
    pages() {
      if (!this.value) return []

      const raw = [...Array(this.pagesCount).keys()].map(page => page + 1)

      if (this.pagesCount < this.maxPagesHalf + 3) return raw
      else {
        if (this.current <= Math.round(this.maxPagesHalf / 2))
          return [
            ...raw.slice(0, this.maxPagesHalf),
            '...',
            raw[raw.length - 1],
          ]
        else if (this.current > this.pagesCount - this.maxPagesHalf + 1)
          return [
            raw[0],
            '...',
            ...raw.slice(raw.length - this.maxPagesHalf, raw.length),
          ]
        else
          return [
            raw[0],
            '...',
            ...raw.slice(
              this.current + 2 - this.maxPagesHalf / 2,
              this.current + this.maxPagesHalf / 2,
            ),
            '...',
            raw[raw.length - 1],
          ]
      }
    },
    pagesCount() {
      return Math.ceil(this.value.length / this.range)
    },
    listPageItems() {
      return this.value.slice(
        this.current * this.range,
        this.range * (this.current + 1),
      )
    },
  },
}
</script>

<style lang="sass">
@import "../../../../scss/smart-grid"
@import "../../../../scss/variables"

.pagination
    max-width: 100%
    color: white

    .pages
        margin-top: 39px
        font-size: 0.9rem
        display: flex
        justify-content: center

        .page, .page_back, .page_forward
            background-color: $cyan-alpha
            padding: 5px 0
            width: 40px
            text-align: center
            margin-right: 1px
            cursor: pointer
            user-select: none
            transition: background-color .5s

            &:hover
                background-color: $cyan
                transition: none

            &:active
                background-color: $light-blue

            &:first-child
                border-radius: 20px 0 0 20px

            &:last-child
                margin: 0
                border-radius: 0 20px 20px 0

            &_active
                background-color: $light-blue

            &_ellipsis
              display: grid
              place-self: center
              background-color: $cyan-alpha
              padding: 5px 0
              width: 40px
              text-align: center
              margin-right: 1px
              user-select: none

.transactions-table
    width: 100%

    &__items
        margin-top: 30px
        width: 100%
        font-size: .8rem

        &__title
          background-color: $cyan-alpha
          border-radius: 8px 8px 0 0
          margin-bottom: 10px
          letter-spacing: .07em

        &__item
          &-date, &-amount
            font-size: .7rem

        &__item, &__title
            color: white
            width: 100%
            height: 40px
            display: grid
            grid-template-columns: 1.3fr 1fr 1fr 1fr 1fr 1fr

            *
              display: grid
              align-content: center
</style>
