<template>
  <div :class="['form', full ? 'full' : '']">
    <div class="coins_title" v-if="title">
      <label>{{ title }}</label>
    </div>
    <Field
      v-if="type"
      :type="type"
      :id="id"
      :value="value"
      :options="options"
      :secret="secret"
      :index="index"
      :selectWidth="selectWidth"
      :isAddress="isAddress"
      @input="$emit('input', $event)"
    />
    <Label v-if="subtitle" :text="subtitle" class="subtitle" />
  </div>
</template>

<script>
import Field from './Field'
import Label from '../../partial/Label'

export default {
  name: 'Form',
  props: [
    'title',
    'subtitle',
    'type',
    'value',
    'options',
    'full',
    'id',
    'secret',
    'index',
    'selectWidth',
    'isAddress',
  ],
  components: {
    Field,
    Label,
  },
}
</script>

<style lang="sass" scoped>
@import "../../../scss/smart-grid"
@import "../../../scss/variables"

.form
    display: flex
    flex-direction: column
    margin-bottom: 20px

    .coins_title
      display: flex
      width: 100%
      justify-content: space-between

      label
        text-align: left
        color: #FFF
        font-size: 16px
        margin-bottom: 10px

    .subtitle
        color: $light-blue
        opacity: .7
        font-size: .9rem

.full
  width: 100%
</style>
