<template>
  <div class="page">
    <Background />
    <AlertsContainer />
    <Header />
    <div class="container">
      <div class="ticker-canvas">
        <div class="tradingview-widget-container">
          <div class="tradingview-widget-container__widget"></div>
        </div>
      </div>
    </div>
    <div class="container last">
      <Tabs />
      <Wallet v-if="selectedTab == 0" />
      <Deposit v-if="selectedTab == 1" />
      <Withdraw v-if="selectedTab == 2" />
      <Transfer v-if="selectedTab == 3" />
      <History v-if="selectedTab == 4" />
      <Support v-if="selectedTab == 5" />
    </div>
    <Footer />
  </div>
</template>

<script>
import Header from '@/components/header/Header.vue';
import Footer from '@/components/footer/Footer.vue';
import Tabs from '@/components/views/exchange/Tabs.vue';
import AlertsContainer from '@/components/partial/AlertsContainer';
import Wallet from '@/components/views/exchange/tabs/Wallet.vue';
import Deposit from '@/components/views/exchange/tabs/Deposit.vue';
import Withdraw from '@/components/views/exchange/tabs/Withdraw.vue';
import Transfer from '@/components/views/exchange/tabs/Transfer.vue';
import History from '@/components/views/exchange/tabs/History.vue';
import Support from '@/components/views/exchange/tabs/Support.vue';
import Background from '@/components/views/exchange/Background.vue';
import { mapGetters } from 'vuex';

export default {
  name: 'Exchange',
  components: {
    AlertsContainer,
    Header,
    Footer,
    Tabs,
    Wallet,
    Deposit,
    Withdraw,
    Transfer,
    History,
    Support,
    Background,
  },
  computed: {
    ...mapGetters({
      selectedTab: 'popups/exchangeCurrentTab',
    }),
  },
  mounted() {
    if (this.$route.hash === '#wallet') {
      this.$store.commit('popups/SELECT_EXCHANGE_TAB', 0);
    } else if (this.$route.hash === '#deposit') {
      this.$store.commit('popups/SELECT_EXCHANGE_TAB', 1);
    } else if (this.$route.hash === '#withdraw') {
      this.$store.commit('popups/SELECT_EXCHANGE_TAB', 2);
    } else if (this.$route.hash === '#transfer') {
      this.$store.commit('popups/SELECT_EXCHANGE_TAB', 3);
    } else if (this.$route.hash === '#history') {
      this.$store.commit('popups/SELECT_EXCHANGE_TAB', 4);
    } else if (this.$route.hash === '#support') {
      this.$store.commit('popups/SELECT_EXCHANGE_TAB', 5);
    }

    var script = document.createElement('script');
    script.setAttribute('type', 'text/javascript');
    script.setAttribute(
      'src',
      'https://s3.tradingview.com/external-embedding/embed-widget-tickers.js',
    );
    script.setAttribute('async', 'true');
    script.innerText =
      '{"symbols":[{"proName":"BITSTAMP:BTCUSD","title":"BTC/USD"},{"proName":"BITSTAMP:ETHUSD","title":"ETH/USD"},{"description":"LTC/USD","proName":"BITSTAMP:LTCUSD"}],"colorTheme":"dark","isTransparent":true,"locale":"en"}';

    this.$el.querySelector('.tradingview-widget-container').appendChild(script);
  },
};
</script>

