<template>
  <div class="tooltip">
    <span class="tooltiptext">{{ text }}</span>
  </div>
</template>

<script>
export default {
  name: 'Tooltip',
  props: ['text', 'index'],
  methods: {
    show() {
      this.$anime({
        targets: this.$el.querySelector('.tooltiptext'),
        opacity: 1,
        duration: 50,
        easing: 'easeOutExpo',
      });
    },
    hide() {
      this.$anime({
        targets: this.$el.querySelector('.tooltiptext'),
        opacity: 0,
        duration: 50,
        easing: 'easeOutExpo',
      });
    },
    once() {
      const self = this;

      this.$anime({
        targets: this.$el.querySelector('.tooltiptext'),
        opacity: 1,
        duration: 50,
        easing: 'easeOutExpo',
        endDelay: 1200,
        complete() {
          self.$anime({
            targets: self.$el.querySelector('.tooltiptext'),
            opacity: 0,
            duration: 50,
            easing: 'easeOutExpo',
          });
        },
      });
    },
  },
  mounted() {
    this.$root.$on('tooltip:once', index => {
      if (index === this.index)
        this.once();
    });
  },
};
</script>

<style lang="sass" scoped>
@import "../../scss/variables"

.tooltip
  position: relative
  display: inline-block

  .tooltiptext
    width: 140px
    background-color: $blue
    color: #fff
    text-align: center
    border-radius: 6px
    padding: 5px
    position: absolute
    z-index: 1
    bottom: 120%
    left: 50%
    margin-left: -55px
    opacity: 0
    transition: opacity 0.3s
    cursor: default
    pointer-events: none

    &::after
      content: ""
      position: absolute
      top: 100%
      left: 50%
      margin-left: -5px
      border-width: 5px
      border-style: solid
      border-color: $blue transparent transparent transparent
</style>
